// src/components/StartScreen.js
import React from 'react';
import Ball from '../assets/soccerball.png';
import Imoji from '../assets/imoji.png';

export default function StartScreen({ onStart }) {
  return (
    <div className="flex flex-col items-center text-center space-y-2">
      <h2 className="text-white text-base font-medium">가장 빠른 사람은 누구?!</h2>
      <div className="flex items-center justify-center space-x-2">
        <h1 className="text-white text-3xl font-bold">축구공 연타 대결</h1>
        <img src={Imoji} alt="이모지" className="w-8 h-8" />
      </div>
      <button
        onClick={onStart}
        className="cursor-pointer transform hover:scale-110 active:scale-95 transition-transform duration-200 mb-2"
      >
        <img src={Ball} alt="공을 누르면 시작" className="w-40 h-40 mx-auto" />
      </button>
      <p className="text-white text-lg">공을 누르면 시작해요!</p>
    </div>
  );
}
