// src/App.js
import React, { useState, useEffect, useCallback } from 'react';
import './index.css';
import background from './assets/background.jpg';
import CI from './assets/matchup-CI.png';
import StartScreen from './components/StartScreen';
import CountdownScreen from './components/CountdownScreen';
import GameScreen from './components/GameScreen';
import ResultScreen from './components/ResultScreen';

function App() {
  const [screen, setScreen] = useState('start');   // start | countdown | game | finish | result
  const [count, setCount]   = useState(3);         // countdown seconds
  const [score, setScore]   = useState(0);         // click score

  // 1) 시작 → 카운트다운
  const handleStart = () => {
    setCount(3);
    setScore(0);
    setScreen('countdown');
  };

  // 2) 카운트다운 → 게임
  useEffect(() => {
    if (screen === 'countdown') {
      if (count > 0) {
        const t = setTimeout(() => setCount(c => c - 1), 1000);
        return () => clearTimeout(t);
      }
      setScreen('game');
    }
  }, [screen, count]);

  // 3) 게임 종료 트리거
  const handleFinish = useCallback(() => {
    setScreen('finish');
  }, []);

  // 4) ‘게임 끝!’ → 1.5초 뒤 결과 페이지
  useEffect(() => {
    if (screen === 'finish') {
      const t = setTimeout(() => setScreen('result'), 1500);
      return () => clearTimeout(t);
    }
  }, [screen]);

  // 클릭 점수 증가
  const handleGameClick = () => {
    if (screen === 'game') {
      setScore(s => s + 1);
    }
  };

  return (
    <div className="flex justify-center items-center w-screen h-screen bg-black overflow-hidden">
      <div
        className="relative w-full max-w-md sm:max-w-lg md:max-w-xl h-full flex items-center justify-center p-4"
        style={{
          backgroundImage: `url(${background})`,
          backgroundSize: 'cover',
          backgroundPosition: 'center',
        }}
      >
        {/* CI 로고 */}
        <img
          src={CI}
          alt="MatchUp CI"
          className="absolute top-4 left-1/2 transform -translate-x-1/2 w-20 z-10"
        />

        {/* 배경 오버레이 */}
        <div className="absolute inset-0 bg-black opacity-80"></div>

        {/* 화면 콘텐츠 */}
        <div className="relative z-20 w-full">
          {screen === 'start'     && <StartScreen onStart={handleStart} />}
          {screen === 'countdown' && <CountdownScreen count={count} />}
          {screen === 'game'      && (
            <GameScreen onClick={handleGameClick} onFinish={handleFinish} />
          )}
          {screen === 'finish'    && (
            <div className="absolute inset-0 flex items-center justify-center bg-black bg-opacity-80 animate-fade-in z-30">
              <span className="text-6xl font-bold text-white">게임 끝!</span>
            </div>
          )}
          {screen === 'result'    && (
            <ResultScreen score={score} onRetry={handleStart} />
          )}
        </div>
      </div>
    </div>
  );
}

export default App;
