// src/components/GameScreen.js
import React, { useState, useEffect, useRef } from 'react';
import Ball from '../assets/soccerball.png';

export default function GameScreen({ onClick, onFinish }) {
  const containerRef = useRef(null);
  const [bouncing, setBouncing] = useState(false);
  const [particles, setParticles] = useState([]);

  // 5초 뒤 "게임 끝!" 호출
  useEffect(() => {
    const timer = setTimeout(onFinish, 5000);
    return () => clearTimeout(timer);
  }, [onFinish]);

  const handleClick = (e) => {
    onClick();

    // 1) 햅틱 진동 (모바일)
    if (navigator.vibrate) {
      navigator.vibrate(30);
    }

    // 2) bounce 애니메이션
    setBouncing(false);
    requestAnimationFrame(() => setBouncing(true));

    // 3) 파티클 효과
    if (containerRef.current) {
      const rect = containerRef.current.getBoundingClientRect();
      const x = e.clientX - rect.left;
      const y = e.clientY - rect.top;
      const newParts = Array.from({ length: 8 }).map(() => {
        const angle = Math.random() * 2 * Math.PI;
        const dist = 20 + Math.random() * 20;
        return {
          id: Math.random().toString(36).substr(2, 9),
          x,
          y,
          dx: `${Math.cos(angle) * dist}px`,
          dy: `${Math.sin(angle) * dist}px`
        };
      });
      setParticles((prev) => [...prev, ...newParts]);
    }
  };

  const handleBounceEnd = () => setBouncing(false);
  const handleParticleEnd = (id) => setParticles((prev) => prev.filter(p => p.id !== id));

  return (
    <div
      ref={containerRef}
      className="relative flex flex-col items-center space-y-6"
    >
      {/* Progress Bar */}
      <div className="w-11/12 mx-auto bg-gray-300 rounded-full h-6 mb-4 overflow-hidden">
        <div
          className="h-full bg-gradient-to-r from-[#B3FF00] to-[#FF91CA] animate-progress"
        />
      </div>

      {/* Soccer Ball */}
      <button
        onClick={handleClick}
        onAnimationEnd={handleBounceEnd}
        className={`cursor-pointer ${bouncing ? 'animate-bounce-y' : ''}`}
        style={{ width: '160px', height: '160px' }}
      >
        <img src={Ball} alt="축구공" className="w-full h-full" />
      </button>

      {/* Particles */}
      {particles.map(p => (
        <div
          key={p.id}
          className="particle"
          style={{
            '--dx': p.dx,
            '--dy': p.dy,
            left: `${p.x}px`,
            top: `${p.y}px`
          }}
          onAnimationEnd={() => handleParticleEnd(p.id)}
        />
      ))}
    </div>
  );
}
